/* home picture */

.single-pic {
  height: 150px;
  width: 150px;
}

.handle {
  border: 1px solid white;
  z-index: -1;
  height: 150px;
  width: 150px;
}

.handle1 {
  border: 1px solid white;
  height: 150px;
  width: 150px;
}

.home-picture {
  /* display: flex;
  flex-direction: row;
  flex-wrap: wrap; */
  height: 456px;
  width: 452px;
  border: 1px solid pink;
  display: grid;
  grid-template-areas:
  
  “h1 h2 h3”
  
  “h4 h5 h6”
  
  “h7 h8 h9”;
  
  grid-template-rows: 1fr 1fr 1fr;   
  grid-template-columns: 150px 150px 150px;
}

