 /* filter: blur(1px); */
  /* -webkit-filter: blur(1px); */
 position: relative;
 height: 456px;
  width: 456px;
  border: 1px solid pink;
}

.home-picture-after-drag-pic {
  background-image: url("https://res.cloudinary.com/dm63jyidg/image/upload/w_1000,ar_1:1,c_fill,g_auto,e_art:hokusai/v1672625117/portfolio/xj77qb5swnl1kzj02j2c.jpg");
 background-repeat: no-repeat, repeat;
 background-color: #cccccc;
