/* projects */
.projects-1st-column {
    display: flex;
    flex-direction: column;
    width: 100%;
    border-top: 0;
}

.block-2-prj {
  width: 80vw;
  border: 0.3px solid #1E2D3D;
  border-top: 0;
  border-left: 0;
}


.main-body-prj {
  display: flex;
  flex-direction: row;
  padding: 7vh 7vw;
  flex-wrap: wrap;
  overflow: scroll;
  gap: 0 3vw;
  height: 69vh;
}

.prj-card {
  font-size: 0.9em;
  display: flex;
  flex-direction: column;
  width: 25%;
  height: 100%;
  margin: 0 3vh;
  color: white;
}

.img-grid {
  transition: transform .2s;
  width: 100%;
  height: cover;
  /* height: 30%; */
  object-fit: cover;
  height: 200px;
  /* object-fit: contain; */
  border-radius: 10px;
}

.img-grid:hover {
  transform: scale(1.1); /* (120% zoom */
  box-shadow: 0 0 8px rgba(255, 255, 255, 0.6);
}

.prj-photo-box {
  border: 1.5px solid #1E2D3D;
  border-radius: 20px;
  padding: 10px;
  align-content: center;
  margin-top: 10px;
}

.prj-description {
  height: 6em;
  line-height: 20px;
}

.button-row {
  display: flex;
  justify-content: space-around;
  padding: 3%;
}

.button {
  background-color: #1E2D3D;
  color: white;
  font-size: 11px;
  width: 85px;
  height: 25px;
  border-radius: 3px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 1px 3px rgb(0 0 0 / 30%);
}

.button:hover, .submit-button {
  transform: scale(1.1); /* (120% zoom */
  box-shadow: 0 0 8px rgba(255, 255, 255, 0.6);
}

.filter-box {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 5px;
}

.filter-box:hover {
  text-shadow: 1px 1px rgba(6, 239, 14, 0.6);
}

/* contact */

.main-body-contact-sent {
  display: flex;
  height: 100%;
  width: 100%;
  /* padding: 0 20px; */
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.main-body-contact-sent-text {
  text-align: center;
  padding: 10px;
  line-height: 1.7;
}

.main-body-contact {
  display: flex;
  flex-direction: row;
  /* background-color: pink; */
  padding: 23%;
}

.contact-form {
  padding: 10px;
  display: flex;
  flex-direction: column;
  width: 100%;
}

.contact-input, .contact-input2 {
  -webkit-box-shadow: rgba(0, 0, 0, 0.3) 0 1px 3px;
  -moz-box-shadow: rgba(0, 0, 0, 0.3) 0 1px 3px;
  box-shadow: rgba(0, 0, 0, 0.3) 0 1px 3px;
  border-radius: 5px;
  margin: 5px;
}

.contact-input:hover, .contact-input2:hover {
  box-shadow: 0 0 20px rgba(255, 255, 255, 0.6);
}

.button-section {
  margin: 20px;
  display: flex;
  justify-content: center;
}

.submit-button {
  background-color: #1E2D3D;
  color: white;
  font-size: 11px;
  width: 120px;
  height: 30px;
  border-radius: 3px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.main-body-contact-2{
  display: flex;
  flex-direction: column;
  /* width: 20%; */
  padding: 3%;
  flex-wrap: wrap;
}

.box-white-text-long-contact {
  height: 40em;
  padding: 10px 20px;
  border: 0.3px solid #1E2D3D;
  border-top: 0;
  align-content: center;
}



/* navbar */

.full-nav {
  display: flex;
  align-items: center;
  border: 0.3px solid #1E2D3D;
  position: sticky;
  top: 0;
}

.nav-1 {
  width: 20vw;
  text-shadow: 1px 3px #dd82b8;
}

.nav-2 {
  width: 40vw;
  display: flex;
  /* justify-content: flex-end; */
}

.nav-3 {
  display: flex;
  flex-direction: row-reverse;
  width: 40vw
}

.nav-box {
  padding: 10px 20px;
  border: 0.3px solid #1E2D3D;
  align-content: center;
}

/* footer */

.footer {
  /* height: 10vh; */
  display: flex;
  justify-content: center;
  position: fixed;
  bottom: 0;
  border: 0.3px solid #1E2D3D;
}

.foot-1 {
  display: flex;
  flex-direction: row;
  width: 40vw;
}

.foot-2 {
  width: 60vw;
  display: flex;
  flex-direction: row-reverse;
  
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media (max-width: 1185px) {
  .prj-description {
    font-size: 0.8em;
    height: 7em;
  }
  }